@tailwind base;
@tailwind components;
@tailwind utilities;

/* Baloo 2 font (ostaje isti) */
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&display=swap');

body {
  margin: 0;
  font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  font-size: 21px; /* +5px (16 -> 21) */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@layer base {
  :root {
    /* ========================================
       BURNING SEA FESTIVAL - NEW COLOR SYSTEM
       Türkis: #00dcb6 | Gelb: #ffdb1e | Orange: #ffa01e | Lila: #b431c8 | Grau: #373737
       ======================================== */

    /* Background - Dark Festival Vibes */
    --background: 220 20% 10%;
    --foreground: 0 0% 95%;

    /* Primary - Türkis */
    --primary: 166 100% 43%;
    --primary-foreground: 220 20% 10%;
    --primary-glow: 166 100% 50%;

    /* Secondary - Gelb */
    --secondary: 50 100% 56%;
    --secondary-foreground: 220 20% 10%;
    --secondary-light: 50 100% 65%;

    /* Accent */
    --accent: 50 100% 56%;
    --accent-foreground: 220 20% 10%;

    /* Kids */
    --kids-primary: 34 100% 56%;
    --kids-secondary: 34 100% 65%;

    /* Adult */
    --adult-primary: 293 61% 49%;
    --adult-secondary: 293 61% 60%;

    /* Base */
    --card: 220 20% 13%;
    --card-foreground: 0 0% 95%;
    --popover: 220 20% 13%;
    --popover-foreground: 0 0% 95%;
    --muted: 220 15% 18%;
    --muted-foreground: 220 10% 65%;
    --border: 220 15% 25%;
    --input: 220 15% 20%;
    --ring: 166 100% 43%;

    /* Extra */
    --gray-dark: 220 10% 22%;

    /* State */
    --destructive: 0 84% 60%;
    --destructive-foreground: 0 0% 98%;
    --success: 166 100% 43%;
    --success-foreground: 220 20% 10%;

    /* Gradients */
    --gradient-primary: linear-gradient(135deg, #ffdb1e 0%, #00dcb6 100%);
    --gradient-fire: linear-gradient(135deg, #ffa01e 0%, #ffdb1e 100%);
    --gradient-water: linear-gradient(135deg, #00dcb6 0%, #00a890 100%);
    --gradient-sunset: linear-gradient(135deg, #ffdb1e 0%, #00dcb6 50%, #b431c8 100%);
    --gradient-hero: linear-gradient(135deg, rgba(0, 220, 182, 0.2) 0%, rgba(180, 49, 200, 0.2) 100%);
    --gradient-kids: linear-gradient(135deg, #ffa01e 0%, #ffdb1e 100%);
    --gradient-adult: linear-gradient(135deg, #b431c8 0%, #00dcb6 100%);
    --gradient-cta: linear-gradient(135deg, #ffdb1e 0%, #00dcb6 100%);

    --gradient-dark: linear-gradient(135deg, hsl(220 20% 8%) 0%, hsl(220 20% 15%) 100%);
    --gradient-dark-accent: linear-gradient(135deg, hsl(220 20% 10%) 0%, hsl(293 30% 15%) 50%, hsl(166 50% 15%) 100%);

    /* Shadows */
    --shadow-elegant: 0 10px 30px -10px rgba(0, 220, 182, 0.3);
    --shadow-glow-primary: 0 0 40px rgba(0, 220, 182, 0.4);
    --shadow-glow-secondary: 0 0 40px rgba(255, 219, 30, 0.4);
    --shadow-glow-kids: 0 0 40px rgba(255, 160, 30, 0.4);
    --shadow-glow-adult: 0 0 40px rgba(180, 49, 200, 0.4);
    --shadow-soft: 0 4px 20px rgba(0, 0, 0, 0.3);

    /* Animations */
    --transition-smooth: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --transition-bounce: 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);

    --radius: 0.75rem;
  }

  .dark {
    --background: 220 20% 8%;
    --foreground: 0 0% 98%;
    --card: 220 18% 12%;
    --card-foreground: 0 0% 98%;
    --popover: 220 18% 12%;
    --popover-foreground: 0 0% 98%;
    --primary: 166 100% 43%;
    --primary-foreground: 220 20% 10%;
    --secondary: 50 100% 56%;
    --secondary-foreground: 220 20% 10%;
    --muted: 220 15% 18%;
    --muted-foreground: 210 10% 65%;
    --accent: 50 100% 56%;
    --accent-foreground: 220 20% 8%;
    --border: 220 15% 25%;
    --input: 220 15% 20%;
    --ring: 166 100% 43%;
  }
}

@layer base {
  * { @apply border-border; }

  body {
    @apply bg-background text-foreground;
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 21px; /* +5px */
  }

  /* GLOBAL p +5px (ako se negdje koristi <p> bez text-* klasa) */
  p {
    font-size: 21px; /* +5px */
  }

  /* H1/H2 +5px (prebačeno na px da bude tačno +5px) */
  h1 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 53px; /* 48 + 5 */
    font-weight: 700;
    margin-bottom: 1rem;
    color: hsl(var(--primary));
    text-shadow: 3px 3px 8px rgba(0,0,0,0.25);
  }

  h2 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 45px; /* 40 + 5 */
    font-weight: 600;
    margin-bottom: 0.8rem;
    color: hsl(var(--secondary));
    text-shadow: 2px 2px 6px rgba(0,0,0,0.2);
  }

  /* Ostale naslove ostavljam kako su bili (nisu traženi) */
  h3 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 0.6rem;
    color: hsl(var(--accent));
    text-shadow: 2px 2px 4px rgba(0,0,0,0.15);
  }

  h4 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 1.75rem;
    font-weight: 500;
    margin-bottom: 0.5rem;
    color: hsl(var(--kids-primary));
  }

  h5 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 0.4rem;
    color: hsl(var(--kids-secondary));
  }

  h6 {
    font-family: 'Baloo 2', system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
    font-size: 1.25rem;
    font-weight: 500;
    margin-bottom: 0.3rem;
    color: hsl(var(--adult-primary));
  }
}

@layer components {
  .gradient-primary { background: var(--gradient-primary); }
  .gradient-fire { background: var(--gradient-fire); }
  .gradient-water { background: var(--gradient-water); }
  .gradient-sunset { background: var(--gradient-sunset); }
  .gradient-hero { background: var(--gradient-hero); }
  .gradient-dark { background: var(--gradient-dark); }
  .gradient-dark-accent { background: var(--gradient-dark-accent); }

  .text-gradient-primary {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .text-gradient-fire {
    background: var(--gradient-fire);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .text-gradient-water {
    background: var(--gradient-water);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .shadow-glow-primary { box-shadow: var(--shadow-glow-primary); }
  .shadow-glow-secondary { box-shadow: var(--shadow-glow-secondary); }
  .shadow-glow-kids { box-shadow: var(--shadow-glow-kids); }
  .shadow-glow-adult { box-shadow: var(--shadow-glow-adult); }
  .shadow-elegant { box-shadow: var(--shadow-elegant); }
}

@layer utilities {
  .transition-smooth { transition: var(--transition-smooth); }
  .transition-bounce { transition: var(--transition-bounce); }
}

/* Animacije */
@keyframes wave { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-10px); } }
@keyframes float { 0%, 100% { transform: translateY(0px) rotate(0deg); } 50% { transform: translateY(-20px) rotate(3deg); } }
@keyframes pulse-glow { 0%, 100% { opacity: 1; } 50% { opacity: 0.7; } }
@keyframes shimmer { 0% { background-position: -1000px 0; } 100% { background-position: 1000px 0; } }

.animate-wave { animation: wave 3s ease-in-out infinite; }
.animate-float { animation: float 6s ease-in-out infinite; }
.animate-pulse-glow { animation: pulse-glow 2s ease-in-out infinite; }

/* Glassmorphism */
.glass {
  background: rgba(255,255,255,0.06);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.12);
}
.glass-dark {
  background: rgba(0,0,0,0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255,255,255,0.08);
}

/* Ostalo */
.perspective-1000 { perspective: 1000px; }
.hide-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }
.hide-scrollbar::-webkit-scrollbar { display: none; }
html { scroll-behavior: smooth; }

@keyframes gradient-x { 0%, 100% { background-position: 0% 50%; } 50% { background-position: 100% 50%; } }
@keyframes glow-pulse { 0%, 100% { box-shadow: 0 0 20px rgba(0, 220, 182, 0.25); } 50% { box-shadow: 0 0 60px rgba(0, 220, 182, 0.55); } }
@keyframes text-shimmer { 0% { background-position: -200% center; } 100% { background-position: 200% center; } }
@keyframes morph { 0%, 100% { border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%; } 50% { border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%; } }

.animate-gradient-x { animation: gradient-x 15s ease infinite; background-size: 200% 200%; }
.animate-glow-pulse { animation: glow-pulse 3s ease-in-out infinite; }
.animate-text-shimmer { background-size: 200% auto; animation: text-shimmer 3s linear infinite; }
.animate-morph { animation: morph 8s ease-in-out infinite; }

.glass-premium {
  background: rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.22);
}

.text-gradient-animated {
  background: linear-gradient(90deg, #ffdb1e 0%, #00dcb6 25%, #b431c8 50%, #00dcb6 75%, #ffdb1e 100%);
  background-size: 200% auto;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: text-shimmer 4s linear infinite;
}

.card-lift { transition: transform 0.3s ease, box-shadow 0.3s ease; }
.card-lift:hover { transform: translateY(-8px) scale(1.02); box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25); }

@keyframes scroll-bounce { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(10px); } }
@keyframes spin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
.animate-scroll-bounce { animation: scroll-bounce 2s ease-in-out infinite; }
.animate-spin-slow { animation: spin 8s linear infinite; }

@layer base {
  [data-debug-wrapper="true"] { display: contents !important; }

  [data-debug-wrapper="true"] > * {
    margin-left: inherit; margin-right: inherit; margin-top: inherit; margin-bottom: inherit;
    padding-left: inherit; padding-right: inherit; padding-top: inherit; padding-bottom: inherit;
    column-gap: inherit; row-gap: inherit; gap: inherit;
    border-left-width: inherit; border-right-width: inherit; border-top-width: inherit; border-bottom-width: inherit;
    border-left-style: inherit; border-right-style: inherit; border-top-style: inherit; border-bottom-style: inherit;
    border-left-color: inherit; border-right-color: inherit; border-top-color: inherit; border-bottom-color: inherit;
  }
}

@layer base {
  html { font-size: 20px; } /* 16 -> 21 (+5) */
}
